<template>
  <div class="devGroupManage">
		<div class="nav">
			<div>
				<!-- <Input v-model="sendData.name" placeholder="请输入分组名称" style="width: 300px" />
				<Button type="primary" icon="ios-search" style="margin-left: 10px;" @click="search">查询</Button> -->
			</div>
			<Button type="primary" icon="md-add" @click="addModule">新增附件</Button>
		</div>
		<div class="content">
			<div class="box">
				<el-table
				  align='center'
				  :data="tableData"
				  tooltip-effect="dark"
				  style="width:	100%;"
					height="100%"
				  ref="multipleTable">
				  <el-table-column
				    align='center'
				    type="selection"
				    width="50">
				  </el-table-column>
				  <el-table-column
				    prop="displayName"
				    sortable
				    label="名称"
				    width="200">
				  </el-table-column>
					<el-table-column
					  prop="id"
					  sortable
					  label="id"
					  width="150">
					</el-table-column>
					<el-table-column
					  prop="hash"
					  sortable
					  label="hash"
					  width="300">
					</el-table-column>
					<el-table-column
						prop="ossName"
					  sortable
					  label="ossName"
					  min-width="250">
					</el-table-column>
				  <el-table-column
				    align='center'
				    label="操作"
				    width="250">
				  	<template slot-scope="scope">
							<div style="width: 100%;height: 100%;display: flex;align-items: center;justify-content: space-around;">
								<Button v-show="!tableData[scope.$index].address" class="address" type="primary" @click="address(scope.row,scope.$index)">下载地址</Button>
								<Button v-show="tableData[scope.$index].address" :data-clipboard-text="addressText"
								class="addresstext" type="primary" @click="copy">复制地址</Button>
								<Button type="warning" @click="upDataClick(scope.row)">修改</Button>
								<Button type="error"  @click="delClick(scope.row.id,scope.row.displayName)">删除</Button>
							</div>
				  	</template>
				  </el-table-column>
				</el-table>
			</div>
			<div class="page-box">
				<el-pagination
					@size-change="rowsChange"
					@current-change="pageChange"
					:current-page="sendData.page+1"
					:page-sizes="[100,500]"
					:page-size="sendData.rows"
					layout="total, sizes, prev, pager, next, jumper"
					:total="totalRows">
				</el-pagination>
			</div>
		</div>
		
		<el-dialog
		  title="修改分组"
		  :visible.sync="dialogVisible"
		  width="40%">
			<div class="item">
				<span class="title">文件名称</span>
				<Input v-model="name" placeholder="请输入文件名称"/>
			</div>
			<div class="item">
				<span class="title">文件</span>
				<!-- <input type="file" name="" id="file" value="" /> -->
				<el-upload
				  class="upload-demo"
					:show-file-list="true"
				  drag
				  action=""
					:auto-upload="false"
					:on-change="changeFile"
					:on-remove="removeFile"
					:file-list="fileList"
					:limit='1'
				  multiple>
				  <i class="el-icon-upload"></i>
				  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
				</el-upload>
			</div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="addClick">确 定</el-button>
		  </span>
		</el-dialog>
		
  </div>
</template>

<script>
import {attachments,addAttachments,delAttachments,addressAttachments} from '@/api/index.js'

export default{
	name:'devGroupManage',
	data(){
		return{
			fileList:[],
			taskName:'',
			dialogVisible:false,
			addressText:'',
			tableData:[],
			totalRows:0,
			nowId:'',
			file:'',
			name:'',
			sendData:{
				page:0,
				rows:100,
				name:''
			}
		}
	},
	watch:{
		name(){
			if(this.name == ''){
				this.gettaskTemplates()
			}
		}
	},
	created() {
		this.gettaskTemplates()
	},
	methods:{
		address(data,index){
			this.addressText =  ''
			addressAttachments(data.id).then(res => {
				this.tableData[index].address = res.content
				this.addressText =  res.content
			})
		},
		copy(){
			let clipboard = new this.Clipboard(".addresstext")
				clipboard.on('success', e => {
					this.$message.success('复制成功')
					clipboard.destroy()
			})	
		},
		removeFile(file,fileList){
			this.fileList = [];
			this.file = ''
			this.fileList = Object.assign(this.fileList,fileList)
		},
		changeFile(file,fileList){
			this.fileList = [];
			this.fileList = Object.assign(this.fileList,fileList)
			this.file = file.raw
		},
		delClick(id,name){
			this.$confirm('此操作将删除：'+ name +', 是否继续?', '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
				delAttachments(id).then(res => {
					this.$message.success('请求已发送')
					this.gettaskTemplates()
				})
			}).catch(() => {
			  this.$message({
			    type: 'info',
			    message: '已取消'
			  });
			});
		},
		upDataClick(data){
			this.nowId = data.id
			this.name = data.displayName
			this.file = ''
			this.fileList = []
			this.dialogVisible = true
		},
		addClick(){
				let formData = new FormData();
				formData.append("name",this.name);
				if(this.file){
					formData.append("file",this.file);
				}
				addAttachments(this.nowId,formData).then(res => {
					this.$message.success('请求已发送')
					this.dialogVisible = false
					this.gettaskTemplates()
				})
		},
		addModule(){
			this.name = ''
			this.file = ''
			this.nowId = ''
			this.fileList = []
			this.dialogVisible = true
		},
		pageChange(page){
			this.sendData.page = page-1
			this.gettaskTemplates()
		},
		rowsChange(rows){
			this.sendData.rows = rows
			this.gettaskTemplates()
		},
		gettaskTemplates(){
			attachments(this.sendData).then(res => {
				this.tableData = res.content.content
				this.totalRows = res.content.totalElements
			})
		}
	},
	filters: {
	  timeHendel(val) {
	    if (val === null) {
	      return "";
	    }
	    val = Number(val + "");
	    var date = new Date(val); // 时间戳为10位需*1000，时间戳为13位的话不需乘1000
	    // console.log('触发了时间格式方法'+ val)
	    var Y = date.getFullYear() + "-";
	    var M =
	      (date.getMonth() + 1 < 10
	        ? "0" + (date.getMonth() + 1)
	        : date.getMonth() + 1) + "-";
	    var D =
	      (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";
	    var h =
	      (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
	    var m =
	      (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
	      ":";
	    var s =
	      date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
	    // console.log('触发了时间格式方法'+ Y + M + D + h + m + s)
	    return Y + M + D + h + m + s;
	  },
	},
}
</script>

<style scoped lang="scss">
	.devGroupManage{
		position: relative;
		width: 100%;
		height: 100%;
		padding: 20px;
		box-sizing: border-box;
		.nav{
			position: relative;
			width: 100%;
			height: 64px;
			padding: 15px 20px;
			box-sizing: border-box;
			background: #FFFFFF;
			border-radius: 4px;
			display: flex;
			justify-content: space-between;
		}
		.content{
			position: relative;
			width: 100%;
			height: calc(100% - 84px);
			margin-top: 20px;
			padding: 15px;
			box-sizing: border-box;
			background: #fff;
			border-radius: 4px;
			.box{
				position: relative;
				width: 100%;
				height: calc(100% - 60px);
				.light {
				  position: relative;
				  display: inline-block;
				  width: 10px;
				  height: 10px;
				  background: rgb(49, 201, 57);
				  border-radius: 50%;
				}
				.dark {
				  position: relative;
				  display: inline-block;
				  width: 10px;
				  height: 10px;
				  background: rgb(153, 153, 153);
				  border-radius: 50%;
				}
			}
			.page-box{
				position: relative;
				width: 100%;
				height: 70px;
				display: flex;
				align-items: center;
				justify-content: flex-end;
			}
		}
		.item{
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			margin-bottom: 20px;
			.title{
				position: relative;
				font-size: 14px;
				color: red;
			}
			.input{
				width: 100%;
			}
		}
	}
</style>
